.btn {
  @include pxRem(padding, 17 30);
  @include pxRem(border-radius, 8);
  @include pxRem(font-size, 15);
  // @include pxRem(height, 40);
  background: map-get($colors, primary);
  color: map-get($colors, white);
  line-height: 1;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  border: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  &:hover {
    background: map-get($colors, primary-dark);
  }
  &:focus {
    outline: none;
  }
  &.disabled,
  &:disabled {
    pointer-events: none;
    color: map-get($colors, txt-disabled);
    border-color: map-get($colors, txt-disabled);
    background: map-get($colors, bg-gray-light);
  }
}

@include respond-below(small) {
  .btn {
    @include pxRem(padding, 15 25);
  }
}

.btn-primary {
  background-color: #00a79d;
}

.btn-primary:hover {
  background-color: #5466f9;
}

.btn-secondary {
  background-color: #5466f9;
}

.btn-secondary:hover {
  background-color: #00a79d;
}
